<template>   
    <v-container>     
        <v-card>       
            <v-container grid-list-md mb-12>
            <h2 class="text-md-center">Data Admin</h2>         
            <v-layout row wrap style="margin:10px">           
                <v-flex xs6 class="text-right">               
                    <v-text-field                 
                    v-model="keyword"                 
                    append-icon="mdi-search"                 
                    label="Search"                 
                    hide-details               
                    ></v-text-field>           
                </v-flex> 
                            
                <v-flex offset-xs4>           
                    <v-btn               
                    depressed               
                    dark               
                    rounded         
                    style="text-transform: none !important;"               
                    color = "green accent-3"               
                    @click="dialog = true"               
                    >               
                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>                   
                        Add Admin               
                    </v-btn>           
                </v-flex> <v-flex>          
                </v-flex>  
                <v-flex offset-xs0>           
                    <v-btn               
                    depressed               
                    dark               
                    rounded
                    href="http://localhost:8080/RESTSERVER_PAW/index.php/Report/admin"         
                    style="text-transform: none !important;"               
                    color = "green accent-3"              
                    >               
                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>                   
                        Report               
                    </v-btn>           
                </v-flex>                  
            </v-layout> 
 
            <v-data-table             
            :headers="headers"             
            :items="users"             
            :search="keyword"             
            :loading="load"           
            >             
            <template v-slot:body="{ items }">               
                <tbody>                 
                    <tr v-for="(item) in items" :key="item.id">                   
                        <td>{{ item.id }}</td>                   
                        <td>{{ item.name }}</td>                   
                        <td>{{ item.email}}</td>                   
                        <td>{{ item.birthdate }}</td>                   
                        <td>{{ item.gender }}</td>                   
                        <td>{{ item.address }}</td>                   
                        <td >                     
                            <v-btn                        
                            icon                       
                            color="indigo"                       
                            light                       
                            @click="editHandler(item)"                     
                            >                       
                            <v-icon>mdi-pencil</v-icon>                     
                            </v-btn>                     
                            <v-btn 
                            icon                       
                            color="error"                       
                            light                       
                            @click="deleteData(item.id)"                     
                            >                       
                                <v-icon>mdi-delete</v-icon>                     
                            </v-btn>                   
                        </td>                 
                    </tr>               
                </tbody>             
            </template>           
        </v-data-table>       
    </v-container>     
    </v-card>     
    <v-dialog v-model="dialog" persistent max-width="600px">       
        <v-card>         
            <v-card-title>           
                <span class="headline">Admin</span>         
                </v-card-title>         
                <v-card-text>           
                    <v-container>             
                        <v-row>               
                            <v-col cols="12">                 
                                <v-text-field filled
                                                rounded label="Name*" v-model="form.name" required></v-text-field>               
                                </v-col>              
                                    <v-col cols="12">                 
                                        <v-menu
                                            v-model="menu2"
                                            :close-on-content-click="false"
                                            :nudge-right="40"
                                            transition="scale-transition"
                                            offset-y
                                            min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                            <v-text-field
                                                v-model="form.birthdate"
                                                label="Birthdate*"
                                                prepend-inner-icon="mdi-calendar"
                                                readonly
                                                filled
                                                rounded
                                                v-on="on"
                                            ></v-text-field>
                                            </template>
                                            <v-date-picker v-model="form.birthdate" @input="menu2 = false"></v-date-picker>
                                        </v-menu>               
                                    </v-col>             
                                    <v-col cols="12">                 
                                        <v-select
                                            v-model="form.gender"
                                            :items="items"
                                            label="Gender*"
                                            filled
                                            rounded
                                            dense
                                        ></v-select>               
                                    </v-col>
                                    <v-col cols="12">                 
                                        <v-text-field filled
                                                rounded label="Address*" v-model="form.address" required></v-text-field>               
                                    </v-col>
                                    <v-col cols="12">                 
                                        <v-text-field filled
                                                rounded
                                                label="Email*" v-model="form.email" required></v-text-field>               
                                    </v-col>               
                                    <v-col cols="12" :hidden="pass">                 
                                        <v-text-field filled
                                                rounded
                                                label="Password*" v-model="form.password" type="password" required></v-text-field>               
                                    </v-col>            
                                    </v-row>           
                                </v-container>           
                                <small>*indicates required field</small>         
                            </v-card-text>         
                            <v-card-actions>           
                                <v-spacer></v-spacer>           
                                <v-btn color="blue darken-1" text @click="resetForm()">Close</v-btn>           
                                <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>         
                                </v-card-actions>       
                            </v-card>     
                        </v-dialog>     
                    <v-snackbar       
                    v-model="snackbar" 
                    :color="color"       
                    :multi-line="true"       
                    :timeout="3000"     
                    >       
                    {{ text }}       
                    <v-btn         
                     dark         
                     text         
                     @click="snackbar = false"       
                    >         
                        Close       
                    </v-btn>     
                </v-snackbar>   
            </v-container> 
        </template> 
<script> 
import { log } from 'util'
export default {    
    data () {       
        return {  
            menu2: false,      
            dialog: false,  
            pass: false,       
            keyword: '', 
            items: ['Male', 'Female'],        
            headers: [             
                {               
                    text: 'ID',               
                    value: 'id',             
                },             
                {               
                    text: 'Name',               
                    value: 'name'             
                },             
                {               
                    text: 'Email',               
                    value: 'email'             
                },             
                {               
                    text: 'Birthdate',               
                    value: 'birthdate'             
                },             
                {               
                    text: 'Gender',               
                    value: 'gender'             
                },             
                {               
                    text: 'Address',               
                    value: 'address'             
                },             
                {               
                    text: 'Aksi',               
                    value: null             
                },         
            ],         
            users: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false, 
            form: {            
                name : '',  
                gender: '',         
                address: '',         
                email : '',           
                password : '',
                birthdate:''        
            },         
            user : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{         
        getData(){
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }             
            var uri = this.$apiUrl + '/User/admin'             
            this.$http.get(uri).then(response =>{                 
                this.users=response.data.message             
            })               
        },         
        sendData(){             
            this.user.append('name', this.form.name);             
            this.user.append('birthdate', this.form.birthdate);             
            this.user.append('gender', this.form.gender);             
            this.user.append('address', this.form.address);             
            this.user.append('email', this.form.email);             
            this.user.append('password', this.form.password);             
            this.user.append('role', 'Admin');            
            var uri =this.$apiUrl + '/User/register'             
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{               
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();           
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;           
            })         
        },         
        updateData(){             
            this.user.append('name', this.form.name);             
            this.user.append('birthdate', this.birthdate);             
            this.user.append('gender', this.form.gender);             
            this.user.append('address', this.form.address);             
            this.user.append('email', this.form.email);             
            this.user.append('password', this.form.password);       
            var uri = this.$apiUrl + '/User/register/' + this.updatedId;             
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{ 
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();               
                this.typeInput = 'new';
                this.pass = false;         
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;               
                this.typeInput = 'new';
                this.pass = false;           
            })         
        },         
        editHandler(item){           
            this.typeInput = 'edit';
            this.pass = true;           
            this.dialog = true;           
            this.form.name = item.name;           
            this.form.email = item.email;           
            this.form.birthdate = item.birthdate;
            this.form.gender = item.gender;           
            this.form.address = item.address;           
            this.updatedId = item.id;         
        },
        
        deleteData(deleteId){ //mengahapus data
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }              
            var uri = this.$apiUrl + '/User/' + deleteId; //data dihapus berdasarkan id 
            this.$http.delete(uri).then(response =>{                 
                this.snackbar = true;                 
                this.text = response.data.message;                 
                this.color = 'green'                 
                this.deleteDialog = false;                 
                this.getData();             
            }).catch(error =>{                 
                this.errors = error                 
                this.snackbar = true;                 
                this.text = 'Try Again';                 
                this.color = 'red'; 
            })         
        },
        
        setForm(){             
            if (this.typeInput === 'new') {               
                this.sendData()             
            } else {               
                this.updateData()             
            }         
        },         
        resetForm(){             
            this.form = {               
                name : '',               
                email : '',               
                password : '',
                gender: '',         
                address: '',         
                email : '',           
                password : ''             
            }
            this.dialog = false
            this.pass = false           
        }     
    },     
    mounted(){         
        this.getData();     
        }, 
    } 
</script> 
  